.App {
  text-align: center;
  width: 100%;
  overflow-x: clip;
}

.App-header {
  background-color: #efebdd;
  /* Footer height is 69px */
  min-height: calc(95vh - 69px);
  display: flex;
  flex-direction: column;
  align-items: center;
  /* justify-content: center; */
  font-size: calc(10px + 2vmin);
  color: #343434;
  width: 100%;
  position: relative;
}

.App-container {
  margin-left: 10vw;
  margin-right: 10vw;
}

.App-link {
  color: #61dafb;
}

.navbar-container {
  position: relative;
}

a {
  text-decoration: none!important;
}

